.input {
  display: block;
  width: 100%;
  height: 32px;
  padding-left: 8px;
  padding-right: 8px;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #888;
  font-size: 16px;
  user-select: none;
  transition: box-shadow 0.3s;

  &:focus {
    box-shadow: 0 0 2px 2px #aaa;
  }

  &[disabled] {
    opacity: 0.5;
  }

  &.isInvalid {
    border-color: #c41212;

    &:focus {
      box-shadow: 0 0 2px 2px #e65050;
    }
  }
}
