.select {
  display: block;
  width: 100%;
  height: 32px;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #888;
  padding-left: 4px;
  padding-right: 24px;
  font-size: 16px;
  user-select: none;

  &:focus {
    box-shadow: 0 0 2px 2px #aaa;
  }

  &[disabled] {
    opacity: 0.5;
  }
}
