.textarea {
  display: block;
  width: 100%;
  min-height: 120px;
  padding: 4px 8px;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #888;
  font-size: 16px;
  letter-spacing: -1px;
  user-select: none;
  resize: none;
  transition: box-shadow 0.3s;

  &:focus {
    box-shadow: 0 0 2px 2px #aaa;
  }

  &[disabled] {
    opacity: 0.5;
  }
}
