.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 420px;
  padding: 48px 24px 24px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 8px 2px #444;
  text-align: center;

  &__title {
    margin-bottom: 12px;
  }

  &__actions {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 16px;
    margin-top: 24px;
  }
}
