.button {
  display: block;
  width: 100%;
  padding: 8px;
  border-radius: 4px;
  background-color: #007000;
  color: #fff;
  transition: 0.3s;
  cursor: pointer;

  &:hover {
    background-color: #008000;
  }

  &:active {
    background-color: #006000;
  }

  &:focus-visible {
    box-shadow: 0 0 2px 2px #888;
  }

  &--secondary {
    background-color: #596559;

    &:hover {
      background-color: #758075;
    }

    &:active {
      background-color: #4e574e;
    }
  }
}
