.board__column {
  position: relative;
  width: calc(100% / 5);
  padding: 0 8px;

  & + & {
    border-left: 1px solid #888;
  }

  &.is-droppable {
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      width: calc(100% - 16px);
      height: 1px;
      background-color: transparent;
      box-shadow: 0 -5px 6px 2px #ccc;
    }
  }
}
