.card {
  position: relative;
  display: flex;
  padding: 8px;
  margin: 8px 0;
  border-radius: 8px;
  border: 1px solid #888;
  background-color: #fff;
  user-select: none;
  transition: box-shadow 0.3s;

  &:hover {
    box-shadow: 0 0 6px 2px #bbb;
  }

  &__inner {
    flex: 1 1 auto;
    width: calc(100% - 24px);
  }

  &__title {
    font-size: 14px;
    margin-bottom: 8px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__assignee {
    font-size: 12px;
    color: #666;
    margin-bottom: 8px;

    &-employee {
      font-weight: 600;
      color: #000;
    }
  }

  &__status {
    display: flex;
  }

  &__menu {
    position: relative;
    flex: 0 0 auto;
    width: 24px;
    height: 24px;
    border: 1px solid transparent;
    border-radius: 4px;
    color: #000;
    transition: border-color 0.3s;

    &:hover {
      border-color: #888;
    }

    &:hover > .card__dropdown {
      transform: translateY(0);
      opacity: 1;
      z-index: 0;
    }

    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: currentColor;
      box-shadow: 6px 0 0 0 currentColor, -6px 0 0 0 currentColor;
    }
  }

  &__dropdown {
    position: absolute;
    top: 0;
    right: 100%;
    white-space: nowrap;
    padding-right: 6px;
    background-color: transparent;
    opacity: 0;
    transform: translateY(10px);
    transition: 0.5s;
    z-index: -1;

    &-inner {
      background-color: #fff;
      border: 1px solid #888;
      border-radius: 4px;
      box-shadow: 0 0 2px 1px #bbb;
      overflow: hidden;
    }

    &-button {
      display: flex;
      align-items: center;
      gap: 8px;
      width: 100%;
      padding: 4px 8px;
      background-color: #fff;
      font-size: 12px;
      line-height: 16px;
      text-align: left;
      transition: background-color 0.3s;
      cursor: pointer;

      &:hover {
        background-color: #eee;
      }
    }
  }

  &[data-drag='true'] {
    box-shadow: none;
    border: none;
    opacity: 0.5;
  }
}
